<script>
export default {
  data() {
    return {
      iconUpDown:{//
        "icon-arrow-down":true,
        "icon-ico_back":false
      },
      show:false,//
      UserName:sessionStorage.getItem("username"),//
      isHoster:sessionStorage.getItem("isHoster")//
    };
  },
  props:{
    isIndex:{default:0}
  },
  methods: {
    // 
    login(){
      this.$router.push("/Login_go");
    },
    // 
    reg(){
      this.$router.push("/Reg_go");
    },
    // 
    Up(){
      this.iconUpDown={
        "icon-arrow-down":false,
        "icon-ico_back":true
      }
      this.show=true;
    },
    //
    Down(){
      this.iconUpDown={
        "icon-arrow-down":true,
        "icon-ico_back":false
      }
      this.show=false;
    },
    //
    GoRouter(e){
      var router=e.target.dataset.router;
      if(router=="close"){
        sessionStorage.clear();
        this.UserName="";
        this.$router.push("/index");
      }else{
        this.$router.push("/"+router);
      }
    },
    
  }
};
</script>
